:root {
  --main-color: rgb(44, 117, 255);
  --secondary-color: rgb(255,60,25);
  --territiary-color: rgb(0,88,255);
}
body, html {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 100%;
  background-color: var(--territiary-color);
}

.App {
  max-width: 960px;
  width: 80%;
  height: 80%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: stretch;
      align-items: stretch;
}


header h1 {
  text-align: center;
}

.desc {
  text-align: center;
}

#root {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

#editor, #preview {
  border: none;
  padding: 5px;
  width: 100%;
  height: 5em;
  overflow: auto;
  -webkit-box-shadow: -1px 1px 5px 9px rgba(44,117,255);
          box-shadow: -1px 1px 5px 9px rgba(44,117,255);
  background-color: rgba(0,0,0,0.2);
}

@media screen and (min-width: 800px){
 
    .App {
      -ms-flex-direction: row;
          flex-direction: row;
      -ms-flex-wrap: wrap;
          flex-wrap: wrap;
      -ms-flex-align: stretch;
          align-items: stretch;
      -ms-flex-pack: justify;
          justify-content: space-between;;
    }

    .wrapper {
      width: 40%;
      display:-ms-flexbox;
      display:flex;
      -ms-flex-direction: column;
          flex-direction: column;
      -ms-flex-pack: stretch;
          justify-content: stretch;
    }

    header{
      width: 100%;
  }

  #editor, #preview {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -ms-flex-positive: 1;
        flex-grow: 1;
  }
}


